#anchors {
  padding: 30px 0;
  box-shadow: $box-shadow;

  & + .section.has_iframe {
    padding-top: 80px;
  }

  .carousel-container {
    position: relative;
    .swiper {
      margin-right: 20px;
      .swiper-wrapper {
        align-items: center;
        text-align: center;

        .swiper-slide {
          width: auto;
          padding: 0 30px;
          font-size: $fontSizeSmall;
          font-weight: bold;

          a {
            &:hover {
              color: $primary-color;
            }
          }
        }
      }
    }

    .swiper-button-prev {
      left: 0;
      transform: translateX(-100%);
    }

    .swiper-button-next {
      right: 0;
      transform: translateX(100%);
    }
  }
}
