* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html {
  margin-top: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif !important;
  color: #333333;
}
body a {
  color: #000000;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
ul li a {
  text-decoration: none;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 5rem;
  padding-left: 5rem;
  max-width: 1360px;
}
@media (max-width: 767px) {
  .container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.section {
  scroll-margin: 100px;
}
.section.default {
  background-color: rgba(255, 255, 255, 0.7);
}
.section.light {
  background-color: #f9fcfe;
  padding-top: 80px;
}
@media (max-width: 1023px) {
  .section.light {
    padding-top: 60px;
  }
}
.section.dark {
  background-color: #222222;
  padding-top: 80px;
}
@media (max-width: 1023px) {
  .section.dark {
    padding-top: 60px;
  }
}
.section.light + .section, .section.dark + .section {
  padding-top: 80px;
}
@media (max-width: 1023px) {
  .section.light + .section, .section.dark + .section {
    padding-top: 60px;
  }
}
.section.has_iframe iframe {
  width: 102%;
  margin: 0 -1%;
}
.section + .section {
  padding-bottom: 80px;
}
@media (max-width: 1023px) {
  .section + .section {
    padding-bottom: 60px;
  }
}
.section:not(#main_content, #contatti, #map) {
  box-shadow: 0 8px 32px -8px rgba(82, 171, 227, 0.2);
}

.light_bg {
  background-color: white;
}

.hidden {
  opacity: 0;
}

.the_title {
  color: #52abe3;
  font-size: 64px;
  font-weight: bolder;
  text-align: center;
  margin-top: 0;
  margin-bottom: 30px;
  line-height: 120%;
}
@media (min-width: 1024px) and (max-width: 1440px) {
  .the_title {
    font-size: 48px;
  }
}
@media (max-width: 1023px) {
  .the_title {
    font-size: 32px;
  }
}

.the_subtitle {
  text-align: center;
  text-transform: uppercase;
  font-family: "Poppins", serif;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 60px;
  line-height: 120%;
}
@media (max-width: 1023px) {
  .the_subtitle {
    font-size: 16px;
    margin-bottom: 40px;
  }
}

.section_title {
  color: #52abe3;
  font-size: 48px;
  font-weight: bolder;
  text-align: center;
  margin-top: 0;
  margin-bottom: 60px;
}
@media (max-width: 1023px) {
  .section_title {
    font-size: 26px;
    margin-bottom: 40px;
  }
}

.has_iframe .section_title {
  margin-bottom: 40px;
}

.prev_title {
  font-family: "Poppins", serif;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  line-height: 120%;
}
@media (max-width: 1023px) {
  .prev_title {
    font-size: 16px;
    margin-bottom: 20px;
  }
}

.description,
.the_content,
p {
  line-height: 160%;
  font-family: "Poppins", serif;
}

@media (min-width: 1440px) {
  .the_content {
    font-size: 18px;
  }
}

.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 767px) {
  .btn {
    width: 100%;
  }
}

.btn_internal {
  min-width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.btn_internal a {
  display: inline-block;
  border-radius: 30px;
  width: 100%;
  padding: 10px 25px;
  color: white;
  background: #52abe3;
  border: 2px solid #52abe3;
  font-family: "Poppins", serif;
  text-transform: capitalize;
  transition: all 0.3s ease-in-out;
}
.btn_internal a:hover {
  color: #52abe3;
  background: transparent;
  border: 2px solid #52abe3;
}

.btn_external {
  min-width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.btn_external a {
  display: inline-block;
  border-radius: 30px;
  width: 100%;
  padding: 10px 25px;
  color: #52abe3;
  background: white;
  border: 2px solid #52abe3;
  font-family: "Poppins", serif;
  text-transform: capitalize;
  transition: all 0.3s ease-in-out;
}
.btn_external a:hover {
  color: white;
  background: #52abe3;
  border: 2px solid #52abe3;
}

.btn_pdf {
  min-width: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.btn_pdf a {
  display: inline-block;
  border-radius: 30px;
  width: 100%;
  padding: 10px 25px;
  color: #52abe3;
  background: white;
  border: 2px solid #52abe3;
  font-family: "Poppins", serif;
  text-transform: capitalize;
  transition: all 0.3s ease-in-out;
}
.btn_pdf a:hover {
  color: white;
  background: #52abe3;
  border: 2px solid #52abe3;
}
.btn_pdf .btn_icon::before {
  content: "\f1c1";
  font-family: "Font Awesome 6 Free";
  margin-right: 10px;
  color: #52abe3;
  font-size: 20px;
}
.btn_pdf a:hover .btn_icon::before {
  color: white;
}

#prossima_partita,
.prossima_partita {
  position: relative;
  overflow: hidden;
}
#prossima_partita:after,
.prossima_partita:after {
  content: url(../img/svg/soccer-ball-svgrepo-com.svg);
  width: 200px;
  height: 200px;
  position: absolute;
  top: 70px;
  left: 0;
  opacity: 0.2;
  z-index: -1;
  scale: 3.5;
}

.btt {
  display: none;
  position: fixed;
  bottom: 30px;
  left: 15px;
  z-index: 100;
}
.btt:hover {
  cursor: pointer;
}
.btt span:after {
  content: "\f077";
  font-family: "Font Awesome 6 Free";
  font-weight: bold;
  color: #c9c8c8;
  padding: 20px;
  background-color: #fff;
  border-radius: 16px;
  border: 1px solid #52abe3;
}

#squad_list {
  padding-bottom: 80px;
}
@media (max-width: 1023px) {
  #squad_list {
    padding-bottom: 60px;
  }
}

#header {
  height: 130px;
  width: 100%;
  z-index: 100;
  background-color: white;
  position: sticky;
  top: 0;
  left: 0;
  transition: all 0.3s ease-in-out;
}
#header + .section {
  padding-top: 80px;
  padding-bottom: 80px;
}
@media (max-width: 1023px) {
  #header + .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}
@media (max-width: 1023px) {
  #header {
    height: 100px;
  }
}
#header:after {
  display: block;
  position: absolute;
  content: "";
  bottom: -0.5rem;
  left: 0;
  right: 0;
  height: 0.5rem;
  z-index: -1;
  background: linear-gradient(270deg, white 0, white 12%, #52abe3 17%, #52abe3 83%, white 88%, white);
}
@media (max-width: 1023px) {
  #header:after {
    background: linear-gradient(270deg, white 0, white 0%, #52abe3 17%, #52abe3 83%, white 100%, white);
  }
}
#header.container {
  max-width: 100%;
}
#header.fixed {
  height: 100px;
  position: fixed;
}
#header.fixed .header_wrapper {
  margin-right: auto;
  margin-left: auto;
  padding-right: 2rem;
  padding-left: 2rem;
  max-width: 1360px;
}
@media (max-width: 767px) {
  #header.fixed .header_wrapper {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
@media (min-width: 1440px) {
  #header.fixed .header_wrapper {
    padding-left: 4rem;
    padding-right: 4rem;
  }
}
#header.fixed .header_wrapper .header_top {
  transition: all 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  #header.fixed .header_wrapper .header_top {
    height: 0;
    visibility: collapse;
    opacity: 0;
  }
}
@media (min-width: 1024px) {
  #header.fixed .header_wrapper .header_bottom #logo {
    height: 80px;
    transform: translateY(15%);
  }
  #header.fixed .header_wrapper .header_bottom #logo img {
    height: 100%;
    transition: all 0.3s ease-in-out;
  }
}
#header.fixed .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu {
  top: 100%;
}
#header.fixed .header_wrapper .header_bottom #sidebar_menu {
  top: 100px;
}
@media (min-width: 1024px) {
  #header.fixed .header_wrapper .header_bottom .social {
    align-items: center;
  }
  #header.fixed .header_wrapper .header_bottom .social i {
    transform: translateY(0) translateX(0);
  }
}
#header .header_wrapper {
  display: flex;
  height: 100%;
  flex-direction: column;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  #header .header_wrapper {
    justify-content: space-between;
    width: 90%;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  #header .header_wrapper {
    width: fit-content;
  }
}
#header .header_wrapper .header_bottom {
  display: flex;
  height: 100%;
}
@media (max-width: 1023px) {
  #header .header_wrapper .header_bottom {
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  #header .header_wrapper .header_bottom {
    gap: 40px;
  }
}
@media (min-width: 1440px) {
  #header .header_wrapper .header_bottom {
    gap: 80px;
  }
}
#header .header_wrapper .header_bottom #logo {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  #header .header_wrapper .header_bottom #logo {
    transform: translateY(30%);
  }
}
#header .header_wrapper .header_bottom #logo img {
  width: auto;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 1023px) {
  #header .header_wrapper .header_bottom #logo img {
    height: 80px;
  }
}
#header .header_wrapper .header_bottom #hamburger_menu {
  position: relative;
  z-index: 1;
  text-align: center;
  align-items: center;
}
#header .header_wrapper .header_bottom #hamburger_menu #open_menu {
  position: relative;
  z-index: 1;
  width: 50px;
  height: 50px;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#header .header_wrapper .header_bottom #hamburger_menu #open_menu .line_1 {
  position: relative;
  flex: none;
  width: 40px;
  height: 3px;
  background: #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease;
}
#header .header_wrapper .header_bottom #hamburger_menu #open_menu .line_2 {
  position: absolute;
  z-index: 1;
  top: 50px;
  width: 35px;
  height: 3px;
  background: #000000;
  transition: all 0.4s ease;
}
#header .header_wrapper .header_bottom #hamburger_menu #open_menu .line_3 {
  position: absolute;
  z-index: 1;
  top: 30px;
  width: 35px;
  height: 3px;
  background: #000000;
  transition: all 0.4s ease;
}
@media (min-width: 1024px) {
  #header .header_wrapper .header_bottom #hamburger_menu {
    display: none;
  }
}
#header .header_wrapper .header_bottom #sidebar_menu {
  position: fixed;
  z-index: 5;
  top: 100px;
  padding-bottom: 60px;
  right: 0;
  overflow-y: scroll;
  transform: translateY(-120%);
  transition: all 1s;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(3px);
  width: 100vw;
  height: calc(100vh - 130px);
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap {
  text-align: center;
  max-width: 90vw;
  max-height: 100vh;
  margin: 30px auto 0;
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li {
  list-style: none;
  color: #52b3de;
  font-size: 26px;
  border-bottom: solid 1px #444;
  padding: 0.8rem;
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li:last-child {
  border-bottom: none;
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li.menu-item-has-children {
  position: relative;
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li.menu-item-has-children span:after {
  content: "\f078";
  font-family: "Font Awesome 5 Free";
  font-size: 20px;
  color: #52abe3;
  font-weight: 600;
  display: inline-block;
  position: absolute;
  top: 7px;
  right: 20px;
  transform: translateY(50%);
  transition: all 0.3s ease-in-out;
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li.menu-item-has-children span.open:after {
  content: "\f077";
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li a {
  color: inherit;
  text-decoration: none;
  transition: color 0.4s ease;
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li > .sub-menu {
  max-height: 0;
  overflow: hidden;
  transition: all 0.5s ease-in-out;
}
#header .header_wrapper .header_bottom #sidebar_menu .sidebar_menu_wrap ul > li > .sub-menu li {
  color: white;
  padding: 10px;
  font-size: 24px;
}
#header .header_wrapper .header_bottom #highlight_menu {
  width: calc(100% - 50px);
}
@media (max-width: 1023px) {
  #header .header_wrapper .header_bottom #highlight_menu {
    display: none;
  }
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu {
  height: 100%;
  display: flex;
  white-space: nowrap;
  align-items: center;
  gap: 32px;
}
@media (min-width: 1440px) {
  #header .header_wrapper .header_bottom #highlight_menu ul.menu {
    gap: 48px;
  }
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li.current_page_item > a {
  color: #52abe3;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li a {
  text-transform: uppercase;
  font-weight: bold;
  color: #000000;
  font-size: 16px;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li a:hover {
  color: #52abe3;
}
@media (min-width: 1440px) {
  #header .header_wrapper .header_bottom #highlight_menu ul.menu > li a {
    font-size: 18px;
  }
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu {
  background-color: white;
  position: absolute;
  text-align: left;
  max-height: 0;
  opacity: 0;
  visibility: hidden;
  top: 80%;
  left: 50%;
  min-width: 200px;
  transform: translateX(-50%);
  transition: 0.3s all ease;
  box-shadow: 0 0 5px grey;
  z-index: 100;
  border-radius: 16px;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li {
  transition: 0.3s all ease;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li:first-child {
  border-radius: 16px 16px 0 0;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li:first-child:after {
  content: "";
  width: 100%;
  height: 12px;
  background-color: transparent;
  position: absolute;
  top: -12px;
  left: 0;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li:first-child:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 12px solid white;
  position: absolute;
  top: -12px;
  left: calc(50% - 10px);
  transition: 0.3s all ease;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li:last-child {
  border-radius: 0 0 16px 16px;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li a {
  display: block;
  font-size: 16px;
  font-family: "Poppins", serif;
  font-weight: lighter;
  white-space: nowrap;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  transition: 0.3s all ease;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li:hover {
  background-color: #333333;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li:hover a {
  color: white;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li ul.sub-menu li:hover:before {
  border-bottom: 12px solid #333333;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li:hover ul.sub-menu {
  max-height: 1000px;
  opacity: 1;
  visibility: visible;
  height: fit-content;
  width: fit-content;
}
#header .header_wrapper .header_bottom #highlight_menu ul.menu > li:hover ul.sub-menu li a {
  padding: 10px 20px;
}
#header .header_wrapper .header_bottom .social {
  margin-left: auto;
  display: flex;
  gap: 20px;
  z-index: 20;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 1024px) {
  #header .header_wrapper .header_bottom .social {
    gap: 10px;
  }
}
@media (max-width: 1023px) {
  #header .header_wrapper .header_bottom .social {
    display: none;
  }
}
#header .header_wrapper .header_bottom .social i {
  font-size: 24px;
  color: #52abe3;
}
@media (min-width: 1024px) {
  #header .header_wrapper .header_bottom .social i {
    transform: translateY(100%) translateX(50%);
  }
}
@media (max-width: 1023px) {
  #header .header_wrapper .header_bottom .social i {
    font-size: 32px;
  }
}

